.divForm {
    // display: grid;
    // grid-template-columns: 1fr 1fr;
    // grid-template-rows: 1fr 1fr;
    position: relative;
    background-color: rgba(173, 106, 106, 0.616);
    max-width: 950px;
    // max-width: 1366px;
    text-align: center;
    left: 50%;
    transform: translateX(-50%);
    // top: 200px;
    transition: 1s;

    .infoShowText {
        color: red;
        font-size: 150%;
        font-weight: bold;
        text-shadow: 1px 1px 1px black;
    }

    .form {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        grid-template-rows: repeat(2, 1fr);
        margin: 10px;
        padding: 10px;
        column-gap: 10px;
        // grid-column: 1/5;



        // .label {
        //     padding-right: 10px;
        //     grid-column: 1/5;
        //     grid-row: 1;
        // }

        // .submitBtn {
        //     padding: 20px;
        //     grid-row: 2/3;
        //     grid-column: 1/3;
        // }

        // .btn {
        //     padding: 20px;
        //     grid-row: 2/3;
        //     grid-column: 3/5;
        // }
        .submitBtn {
            // padding: 20px;
            padding: 10px 15px 10px 15px;
            grid-row: 2/3;
            grid-column: 1/3;
            border: 2px solid black;
            background-color: white;
            border-radius: 10px;
            transition: 300ms;
        }

        .submitBtn:hover {
            color: white;
            background-color: black;
        }

        .btn {
            // padding: 20px;
            padding: 10px 15px 10px 15px;
            grid-row: 2/3;
            grid-column: 3/5;
            border: 2px solid black;
            background-color: white;
            border-radius: 10px;
            transition: 300ms;
        }

        .btn:hover {
            color: white;
            background-color: black;
        }
    }

    // .submitBtn {
    //     padding: 20px;
    //     grid-row:2/3;
    //     grid-column: 2;
    // }

    // .btn {
    //     padding: 20px;
    //     grid-row: 2/3;
    //     grid-column: 2;
    // }

}

@media screen and (max-width: 945px) {
    .divForm {
        // background-color: red;
        max-width: 750px;
        transition: 1s;

        .form {
            position: relative;
            display: grid;
            grid-template-columns: 1fr 1fr;
            grid-template-rows: 1fr 1fr 2fr;
            gap: 10px;
            transition: 60s;

            // justify-items: center;
            // align-items: center;

            .label {
                box-shadow: 1px black;

            }

            .submitBtn {
                // padding: 20px;
                padding: 10px 15px 10px 15px;
                grid-row: 3;
                grid-column: 1;
                border: 2px solid black;
                background-color: white;
                border-radius: 10px;
                transition: 300ms;
            }

            .submitBtn:hover {
                color: white;
                background-color: black;
            }

            .btn {
                // padding: 20px;
                padding: 10px 15px 10px 15px;
                grid-row: 3;
                grid-column: 2;
                border: 2px solid black;
                background-color: white;
                border-radius: 10px;
                transition: 300ms;
            }

            .btn:hover {
                color: white;
                background-color: black;
            }
        }
    }
}

@media screen and (max-width: 594px) {
    .divForm {
        max-width: 550px;
        transition: 1s;
    }
}

@media screen and (max-width: 520px) {
    .divForm {
        max-width: 500px;
        transition: 1s;
    }
}